@include desktop() {
  .App {
    position: relative;
    font-family: "Nanum Gothic", sans-serif;

    .container {
      width: 90vw;
      margin: 0 auto;
    }

    section {
      padding: 100px 0;
      border-bottom: 2px dashed rgb(206, 184, 184);

      &#contact {
        border: none;
      }

      h2 {
        font-size: 36px;
        font-weight: 700;
        text-align: center;
      }
    }
  }
}
