@include desktop() {
  header {
    position: sticky;
    top: 0;
    left: 0;
    right: 0;
    height: 100px;
    z-index: 9999;

    .header-inner {
      background-color: #fff;
      .container {
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 60px;

        #logo {
          font-family: "Dancing Script";
          font-size: 36px;
        }

        nav {
          display: flex;
          justify-content: space-between;
          width: 500px;
          font-size: 16px;
        }
      }
    }

    // Wave
    .custom-shape-divider-top-1656519075 {
      position: absolute;
      top: 59px;
      left: 0;
      right: 0;
      width: 100%;
      overflow: hidden;
      line-height: 0;
      svg {
        position: relative;
        display: block;
        width: calc(129% + 1.3px);
        height: 60px;
      }
      .shape-fill {
        fill: #fff;
      }
    }
  }
}
