@include mobile() {
  #about {
    .about-inner {
      padding: 10px 30px;
    }

    #introduction {
      height: 350px;
      font-size: 16px;
      font-weight: 600;
      line-height: 1.4;
      text-align: center;
      color: rgba(0, 0, 0, 0.7);

      .typeit {
        height: 70px;
        margin-bottom: 10px;
        word-break: keep-all;
      }

      p {
        font-size: 12px;
        font-weight: 400;
        word-break: keep-all;
      }
    }

    #profile {
      background-color: aliceblue;
      width: 100%;
      margin: 16px auto 0;
      padding: 20px;
      background-color: #fff;
      box-shadow: 1px 1px 8px 1px rgba(0, 0, 0, 0.18);

      h2 {
        margin-bottom: 16px;
        font-size: 18px;
      }

      .profile-img {
        display: none;
      }

      .profile-list {
        font-size: 12px;
        color: rgba(0, 0, 0, 0.7);

        ul {
          li {
            display: flex;
            align-items: center;
            margin-bottom: 6px;

            span {
              padding-left: 10px;
            }
          }

          &:first-child {
            padding-bottom: 10px;
            border-bottom: 1px dashed rgb(206, 184, 184);
          }

          &:last-child {
            padding-top: 14px;
            display: flex;
            justify-content: space-around;
            font-size: 18px;

            span {
              display: none;
            }
          }
        }
      }
    }
  }
}
